export default {
	data() {
		return {
			
		}
	},
	computed: {},
	methods: {
		// 
		async getDom(className, isAll = true) {
			const result = await this.$uniTools.getDom(this, className, isAll);
			return result;
		},
		// 
		getTimeStamp() {
			return +new Date();
		},
		// 
		jumpPage(url, params) {
			this.$uniTools.navigateTo({url, params});
		}
	}
}
